@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:color';

.formulario {
    display: flex;
    flex-direction: column;

    &__fieldset {
        border: none;
        margin-top: 1rem;
        padding: 0;
    }

    &__legend {
        color: v.$gris;
        font-size: 2.2rem;
        margin-bottom: 2rem;
    }

    &__campo {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin-bottom: 1.5rem;
    }

    &__contenedor-icono {
        display: flex;
        border-radius: .5rem;
        overflow: hidden;
    }
    
    &__icono {
        background-color: v.$gris-oscuro;
        width: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;

        i {
            color: v.$blanco;
            font-size: 2rem;
        }
    }

    &__label {
        color: rgba(v.$negro, 0.8);
    }

    &__input,
    &__select {
        padding: 1rem;
        border: 1px solid color.adjust(v.$gris-claro, $lightness: -20%);
        border-radius: .5rem;

        &--file {
            border: none;
        }

        &--sociales {
            @extend .formulario__input;
            flex: 1;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }
    }

    &__radio {
        display: flex;
        gap: 3rem;

        &--centrar {
            display: flex;
            align-items: center;
            gap: 1rem;
        }
    }

    &__submit {
        @include m.boton(v.$azul, v.$blanco);
        width: auto;
        align-self: self-start;

        &--registrar {
            width: 100%;
        }
    }

    &__listado {
        @include m.tags;
    }

    &__texto {
        color: rgba(v.$negro, 0.8);
    }

    &__imagen {
        max-width: 20rem;
    }
}